@import url(https://fonts.googleapis.com/css2?family=Dancing+Script&display=swap);
.mid{font-family:'Arial', sans-serif;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center}.rocker{display:inline-block;position:relative;font-size:2em;font-weight:bold;text-align:center;text-transform:uppercase;color:#888;width:7em;height:4em;overflow:hidden;border-bottom:0.5em solid #eee}.rocker-small{font-size:0.75em;margin:1em}.rocker::before{content:"";position:absolute;top:0.5em;left:0;right:0;bottom:0;background-color:#999;border:0.5em solid #eee;border-bottom:0}.rocker input{opacity:0;width:0;height:0}.switch-left,.switch-right{cursor:pointer;position:absolute;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;height:2.5em;width:3em;transition:0.2s}.switch-left{height:2.4em;width:2.75em;left:0.85em;bottom:0.4em;background-color:#ddd;-webkit-transform:rotate(15deg) skewX(15deg);transform:rotate(15deg) skewX(15deg)}.switch-right{right:0.5em;bottom:0;background-color:#000;color:#fff}.switch-left::before,.switch-right::before{content:"";position:absolute;width:0.4em;height:2.45em;bottom:-0.45em;background-color:#ccc;-webkit-transform:skewY(-65deg);transform:skewY(-65deg)}.switch-left::before{left:-0.4em}.switch-right::before{right:-0.375em;background-color:transparent;-webkit-transform:skewY(65deg);transform:skewY(65deg)}input:checked+.switch-left{background-color:#f2921a;color:#fff;bottom:0px;left:0.5em;height:2.5em;width:3em;-webkit-transform:rotate(0deg) skewX(0deg);transform:rotate(0deg) skewX(0deg)}input:checked+.switch-left::before{background-color:transparent;width:3.0833em}input:checked+.switch-left+.switch-right{background-color:#ddd;color:#888;bottom:0.4em;right:0.8em;height:2.4em;width:2.75em;-webkit-transform:rotate(-15deg) skewX(-15deg);transform:rotate(-15deg) skewX(-15deg)}input:checked+.switch-left+.switch-right::before{background-color:#ccc}input:focus+.switch-left{color:#333}input:checked:focus+.switch-left{color:#fff}input:focus+.switch-left+.switch-right{color:#fff}input:checked:focus+.switch-left+.switch-right{color:#333}

@font-face{src:url(/freecodecamp-drum-machine/static/media/Led-Display.28cd7093.ttf);font-family:"Digital Led"}.drum-machine{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:space-evenly;justify-content:space-evenly;-webkit-align-items:center;align-items:center;width:30rem;height:55rem;background-color:#181e1e;border:5px outset lightgray}.drum-machine h1{border:2px solid lightgrey;padding:0.8rem 2.5rem;font-size:2.5rem;color:white;font-family:'Dancing Script', cursive}.switchSet{width:100%;display:-webkit-flex;display:flex;-webkit-justify-content:space-evenly;justify-content:space-evenly}.switchSet div h4{text-align:center;font-size:1.3rem;font-family:'Dancing Script', cursive;color:white;border:2px solid lightgrey;padding:0.4rem 0}.panel{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;margin-top:1rem;width:22rem;height:6rem;background-color:#649bb8}.panel h2{font-size:1.8rem;font-weight:500;font-family:"Digital Led", sans-serif}.slider-audio{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;grid-gap:0.5rem;gap:0.5rem;font-size:0.875rem;width:20rem;height:3rem}.slider-audio .drum-slier{background-color:white}.buttonSet{display:grid;grid-template-columns:repeat(3, 1fr);grid-column-gap:0.4rem;-webkit-column-gap:0.4rem;column-gap:0.4rem;grid-row-gap:0.4rem;row-gap:0.4rem}.buttonSet .drum-pad{width:8rem;height:8rem;background-color:#666472;font-size:2rem;font-weight:500;color:#fff}.buttonSet .drum-pad:active{color:#000;background-color:#f2921a}.buttonSet .drump-pad-key-press{color:#000;background-color:#f2921a}

